.appear {
  -webkit-animation-name: apearr 1s ease-out;
          animation-name: apearr 1s ease-out;
}

.slide-in {
  -webkit-animation: slide 400ms linear;
          animation: slide 400ms linear;
}

.spin {
  -webkit-animation: spins 1s linear infinite;
          animation: spins 1s linear infinite;
}

.spin-360 {
  -webkit-animation: spinss 15s linear infinite;
          animation: spinss 15s linear infinite;
}

.glow {
  -webkit-animation: glows 3s linear infinite;
          animation: glows 3s linear infinite;
}

.hints-appear {
    transform: scale(0);
  }
  
.hints-appear.hints-appear-active {
    transform: scale(1);
    transition: transform .5s ease-in;
  }

.fade-enter {
    opacity: 0;
    z-index: 1;
  }
  
  .fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 250ms ease-in;
  }

@-webkit-keyframes slide {
  0% {
    transform: translate(60%, 0%);
    opacity: (0);
  }
  100% {
    transform: translate(0, 0);
    opacity: (1);
  }
}

@keyframes slide {
  0% {
    transform: translate(60%, 0%);
    opacity: (0);
  }
  100% {
    transform: translate(0, 0);
    opacity: (1);
  }
}

@-webkit-keyframes apearr {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes apearr {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

@-webkit-keyframes glows {
  0% {
    -webkit-filter: drop-shadow(6px 6px 7px black);
            filter: drop-shadow(6px 6px 7px black);
  }
  50% {
    -webkit-filter: drop-shadow(2px 2px 2px black);
            filter: drop-shadow(2px 2px 2px black);
  }
  100% {
    -webkit-filter: drop-shadow(6px 6px 7px black);
            filter: drop-shadow(6px 6px 7px black);
  }
}

@keyframes glows {
  0% {
    -webkit-filter: drop-shadow(6px 6px 7px black);
            filter: drop-shadow(6px 6px 7px black);
  }
  50% {
    -webkit-filter: drop-shadow(2px 2px 2px black);
            filter: drop-shadow(2px 2px 2px black);
  }
  100% {
    -webkit-filter: drop-shadow(6px 6px 7px black);
            filter: drop-shadow(6px 6px 7px black);
  }
}

@-webkit-keyframes spins {
  0% {
    transform: rotate(20deg);
  }
  100% {
    transform: rotate(110deg);
  }
}

@keyframes spins {
  0% {
    transform: rotate(20deg);
  }
  100% {
    transform: rotate(110deg);
  }
}

@-webkit-keyframes spinss {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(359deg);
  }
}

@keyframes spinss {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(359deg);
  }
}
@media (min-width: 860px) {
  .desktop-margin {
    margin: 0 0 1em 2em;
  }
}

@-webkit-keyframes wave {
  0% {
    margin-left: 0;
  }
  100% {
    margin-left: -1600px;
  }
}

@keyframes wave {
  0% {
    margin-left: 0;
  }
  100% {
    margin-left: -1600px;
  }
}

@-webkit-keyframes swell {
  0%, 100% {
    transform: translate3d(0,-25px,0);
  }
  50% {
    transform: translate3d(0,5px,0);
  }
}

@keyframes swell {
  0%, 100% {
    transform: translate3d(0,-25px,0);
  }
  50% {
    transform: translate3d(0,5px,0);
  }
}
.grid {
    display: grid;
    place-items: center;
  }

  .grid , .header{
    display: grid;
    width: 100%;
    place-items: center;
    grid-template-columns: 1fr;
  }
  
  .start-1{
    grid-column-start: 1;
  }
  
  .start{
    place-items: start;
  }
  
  .span {
    grid-column: 1/-1;
  }

  .span-cols{
    grid-column: span 2;
  }
  
